.t-link--popup-text {
  position: relative;
}
.t-link--popup-text::before, .t-link--popup-text::after {
  font-size: 0.9em;
  background: #fcf300;
  color: black;
  line-height: 1em;
  padding: 0.2em 0.15em 0.2em;
  border-radius: 0.2em;
  position: absolute;
  top: -1em;
  left: 50%;
  opacity: 0;
  transition: 0.2s opacity cubic-bezier(0.33, 1, 0.68, 1), 0.2s transform cubic-bezier(0.33, 1, 0.68, 1);
}
.t-link--popup-text::before {
  content: "Click";
  transform: translate(-65%, 20%) rotateZ(-1deg);
}
.t-link--popup-text::after {
  content: "me!";
  transform: translate(45%, 20%) rotateZ(1deg);
}
.t-link--popup-text:hover::before, .t-link--popup-text:hover::after {
  transition: 0.4s opacity cubic-bezier(0.33, 1, 0.68, 1), 0.4s transform cubic-bezier(0.33, 1, 0.68, 1);
  opacity: 1;
}
.t-link--popup-text:hover::before {
  transform: translatex(-75%) rotateZ(-3deg);
}
.t-link--popup-text:hover::after {
  transform: translatex(45%) rotateZ(3deg);
}

.t-link--fire-gif-background {
  background-position-y: calc(1em + 16px);
  transition: background-position-y 0.6s ease-out, color 0.6s ease-out;
  background-repeat: repeat-x;
  background-image: url("../images/fire-gif-transparent.gif"), linear-gradient(rgba(0, 0, 0, 0.7), black);
  background-size: contain;
  padding: 4px;
}
.t-link--fire-gif-background:hover {
  background-position-y: 0;
  color: white;
}
.t-link--fire-gif-pseudoelement {
  position: relative;
  display: inline-block;
  color: #FCCA46;
  text-shadow: 0px 0px 4px white, 0px 0px 4px white, 0px 0px 4px white;
  transition: color 0.6s ease;
}
.t-link--fire-gif-pseudoelement:hover {
  color: black;
}
.t-link--fire-gif-pseudoelement::before {
  content: "";
  position: absolute;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: -2px;
  z-index: -1;
  background-repeat: repeat-x;
  background-image: url("../images/fire-gif-transparent.gif");
  background-size: auto 100%;
  transform: scaleY(0);
  transform-origin: bottom;
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.3s ease-out;
}
.t-link--fire-gif-pseudoelement:hover::before {
  transform: scaleY(1);
  opacity: 1;
  transition: transform 0.6s ease, opacity 0.3s ease;
}

.t-link--cube-rotation {
  position: relative;
  display: inline-block;
  line-height: 1em;
  font-size: 2em;
}
.t-link--cube-rotation__top {
  display: inline-block;
  position: absolute;
  top: 0;
  transform: translateY(-50%) rotateX(90deg);
  transition: transform 0.6s ease;
}
.t-link--cube-rotation__front {
  display: inline-block;
  transform: translateZ(0.5em);
  transition: transform 0.6s ease;
  text-decoration: underline;
}
.t-link--cube-rotation:hover .t-link--cube-rotation__top {
  transform: translateY(0) translateZ(0.5em);
  color: black;
}
.t-link--cube-rotation:hover .t-link--cube-rotation__front {
  transform: translateY(50%) rotateX(-90deg);
}

.t-link--fill-up-transform {
  position: relative;
  color: transparent;
  z-index: 2;
  font-weight: bold;
  text-decoration: underline;
  font-size: 2em;
}
.t-link--fill-up-transform__outline {
  color: #FCCA46;
  color: transparent;
  -webkit-text-stroke: 1px #FCCA46;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 0.4ex), #fcf300 calc(50% - 0.4ex), #fcf300 100%);
  background-size: 100% 200%;
  background-position: 0 0;
}
.t-link--fill-up-transform__clip {
  display: block;
  transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1);
  overflow: hidden;
  position: absolute;
  top: 0;
  transform: translateY(100%);
  pointer-events: none;
}
.t-link--fill-up-transform__filled {
  display: block;
  transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1);
  color: #FCCA46;
  transform: translateY(-100%);
}
.t-link--fill-up-transform:hover .t-link--fill-up-transform__clip, .t-link--fill-up-transform:hover .t-link--fill-up-transform__filled {
  transform: translateY(0);
}

.t-link--split-in-half {
  position: relative;
  display: inline-block;
  line-height: 1em;
  font-size: 2em;
  /*Can't use this type of underscore for multiple lines of text :( 
  Good for menu buttons though, this would use a transform animation instead of a background position animation, better performance :)
  .t-link--bottom:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 8%;
    background: red;
    transform-origin: left bottom;
    transition: transform 0.2s ease;
  }

  .t-link:hover .t-link--bottom:before {
    transform: scaleX(0%);
  }
  */
}
.t-link--split-in-half--top {
  position: absolute;
  color: #FCCA46;
  top: 0;
  display: inline-block;
  clip-path: polygon(0% 66%, 0% 0%, 100% 0%, 100% 40%);
  transition: transform 0.6s cubic-bezier(0.16, 1, 0.3, 1) 0.25s;
}
.t-link--split-in-half--top:after {
  content: "";
  position: absolute;
  top: 36%;
  left: 0;
  width: 100%;
  height: 8%;
  background: #1e96fc;
  transform: rotateZ(-2.2deg) scaleX(0%);
  transform-origin: right top;
  transition: transform 0.2s ease 0.22s;
}
.t-link--split-in-half--bottom {
  display: inline-block;
  clip-path: polygon(0% 65%, 100% 40%, 100% 110%, 0% 110%);
  text-decoration: underline;
  color: #FCCA46;
  transition: background-position 0.2s ease 0.22s;
  text-decoration: none;
  background-size: 200% 8%;
  background-position: left bottom;
  background-repeat: no-repeat;
  background-image: linear-gradient(to right, #1e96fc 50%, transparent 50%);
}
.t-link--split-in-half--bottom:after {
  content: "";
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: 8%;
  background: #1e96fc;
  transform: rotateZ(-2deg) scaleX(0%);
  transform-origin: right top;
  transition: transform 0.2s ease 0.22s;
}
.t-link--split-in-half:hover .t-link--split-in-half--top {
  transform: translateY(-0.5em) rotateZ(-3deg);
  transition: transform 0.5s cubic-bezier(0.12, 0.8, 0.57, 1) 0.42s;
}
.t-link--split-in-half:hover .t-link--split-in-half--bottom {
  background-position: 100% bottom;
  transition: background-position 0.2s ease;
}
.t-link--split-in-half:hover .t-link--split-in-half--top:after {
  top: 56.5%;
  transform-origin: left top;
  transform: rotateZ(-2.4deg) scaleX(100%);
}
.t-link--split-in-half:hover .t-link--split-in-half--bottom:after {
  top: 65%;
  transform-origin: left top;
  transform: rotateZ(-2.4deg) scaleX(100%);
}

