.tu-scrolltop {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  transform: translate(4rem, 0);
  width: 3.5rem;
  height: 3.5rem;
  display: block;
  cursor: pointer;
  background: #6246E4;
  border-radius: 2rem;
  box-shadow: 0 1.5rem 3rem 0 rgba(50, 39, 115, 0.42), inset 0 -0.5rem 1.5rem 0 rgba(239, 237, 252, 0.8);
  padding: 0.5rem 0.25rem 0.375rem;
  display: block;
  width: 0;
  height: 0;
  max-height: 0;
  overflow: hidden;
  visibility: hidden;
  transition: 0.5s height ease, 0.5s max-height ease, 0.5s opacity ease, 0s visibility ease;
  padding: 0;
  margin: 0;
  border: 0;
  filter: alpha(opacity=0);
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  opacity: 0;
  z-index: 99;
  transition: all 0.25s ease-in-out;
}
