}

.float--change-mode {
  cursor: pointer;
  width: 3.5rem;
  height: 3.5rem;
  position: fixed;
  right: 2rem;
  bottom: 6rem;
  background-color: var(--default);
  box-shadow: 0 1.5rem 3rem 0 rgba(252, 202, 70, 0.42), inset 0 -0.5rem 1.5rem 0 rgba(252, 202, 70, 0.8);
  padding: 0.5rem;
  border-radius: 50%;
  z-index: 99;
  transition: all 0.25s ease-in-out;
}
.float--change-mode:hover, .float--change-mode:focus:hover {
  color: #523cbe;
  opacity: 0.6;
}
.float--change-mode:hover:before, .float--change-mode:focus:hover:before {
  content: "";
  border-radius: inherit;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 1.5rem 3rem 0 rgba(86, 184, 149, 0.42), inset 0 -0.5rem 1.5rem 0 rgba(113, 235, 191, 0.8);
}
.float--change-mode svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 2rem;
  height: 2rem;
  fill: transparent;
  transition: 0.6s background-color ease;
